<template>
    <tab-box-m :top-menu="topMenu" v-loading="loading" :router-data="routerData" @action="action"></tab-box-m>
</template>

<script>
    import TabBoxM from "../../../components/layout/mobile/tabBoxM";
    export default {
        name: "cardMobile",
        components: {TabBoxM},
        data() {
            return {
                loading: true,
                routerData: {
                    p: 1,
                    num: 10,
                    dtCreateStart: '',
                    dtCreateEnd: '',
                    data: {}
                },
            }
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.getData(to);
            next();
        },
        computed: {
            topMenu: {
                get() {
                    let topMenu = {
                        fullView: true,
                    };
                    return topMenu;
                }
            }
        },
        created() {
            if (!this.$isMobile()) {
                this.$router.push({name:'userCard'});
            } else {
                this.getData(this.$route);
            }
        },
        methods: {
            action(data) {
                if (data && data.hasOwnProperty('action')) {
                    switch (data.action) {
                        case 'page':
                            this.$router.push({
                                query: {
                                    p: data.value
                                }
                            });
                            break;
                        case 'time':
                            this.$router.push({
                                query: {
                                    dtCreateStart: data.value.dtCreateStart,
                                    dtCreateEnd: data.value.dtCreateEnd
                                }
                            });
                            break;
                        case 'type':
                            this.$router.push({
                                params: {
                                    type: data.value
                                }
                            });
                            break;
                    }
                }
            },
            getData(router) {
                const _self = this;
                _self.loading = true;

                _self.routerData.data = {};
                _self.routerData.p = 1;
                if (router.query.hasOwnProperty('p')) {
                    _self.routerData.p = parseInt(router.query.p);
                    if (isNaN(_self.routerData.p)) {
                        _self.routerData.p = 1;
                    }
                }
                if (router.query.hasOwnProperty('dtCreateStart') && router.query.hasOwnProperty('dtCreateEnd')) {
                    _self.routerData.dtCreateStart = router.query.dtCreateStart;
                    _self.routerData.dtCreateEnd = router.query.dtCreateEnd;
                }
                let arr = {
                    type: 2,
                    p: _self.routerData.p,
                    dtCreateStart: _self.routerData.dtCreateStart,
                    dtCreateEnd: _self.routerData.dtCreateEnd
                };
                _self.$api('getcardInfo', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.routerData.data = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData(router);
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData(router);
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
        }
    }
</script>

<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>